import React from 'react'
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import TextField from '@mui/material/TextField';
import { Typography } from '@material-ui/core';
import { JSONTree } from 'react-json-tree';

class Webstring extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          input_value: '',
          output_value: '',
          data: {},
          process: []
        };
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({input_value: event.target.value});
    }

    handleSubmit(event) {
        event.preventDefault();
        this.setState({data: JSON.parse(this.state.input_value), output_value: JSON.stringify(this.state.data)});
    }
    
    render() {
        return (
            <Container maxWidth="md" style={{fontFamily: 'monospace'}}>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} >
                Json decode, URL decode, and automagic decode
              </Typography>
              <TextField
                id="standard-multiline-static"
                label="Input"
                multiline
                rows={10}
                defaultValue=""
                variant="standard"
                onChange={this.handleChange}
                sx={{ width: 1, fontFamily: 'monospace' }}
              />
              <Button text="Decode" onClick={this.handleSubmit} sx={{ width: 0.3 }} alignItems="center" justifyContent="center">Decode</Button>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} >
                Syntax Highlight
              </Typography>        
              <SyntaxHighlighter language="json" style={docco}>{this.state.output_value}</SyntaxHighlighter>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} >
                Tree View
              </Typography>        
              <JSONTree data = {this.state.data} invertTheme={true}/>
            </Container>
          )
    }
}

export default Webstring; 
